@import "../../global.scss";

.topbar{
  width: 100%;
  height: 71px;
  background-color: ghostwhite;
  color: $mainColor;
  position: fixed;
  top: 0;
  z-index: 3;
  transition: all 0.5s ease;

  .wrapper{
    padding: 10px 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .left{
      display: flex;
      align-items: center;

      .logo {
        width: 70px;
        height: 47px;
        margin-right: 40px;
      }

      .itemContainer{
        display: flex;
        align-items: center;
        margin-left:30px;

        @include mobile{
          margin-left: 0px;
        }

        .icon{
          font-size: 30px;
          margin-right: 5px;
        }

        a{
          font-size: 15px;
          font-weight: 700;
          text-decoration: none;
          color: inherit;
        }
      }
    }

    .right{
      .hamburger{
        width: 32px;
        height: 25px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        cursor: pointer;

        span{
          width: 100%;
          height: 3px;
          background-color: $mainColor;
          transform-origin: left;
          transition: all 0.5s ease;
        }
      }
    }
  }

  &.active{
    background-color: $mainColor;
    color: ghostwhite;

    .hamburger{
      span{
        &:first-child{
          background-color: ghostwhite;
          transform: rotate(45deg);
        }
        &:nth-child(2){
          opacity: 0;
        }
        &:last-child{
          background-color: ghostwhite;
          transform: rotate(-45deg);
        }
      }
    }
  }
}